import React, { useRef, useEffect } from 'react'
import tippy from 'tippy.js'
import { Button } from '../Framework'

const arr = Array(16).fill()
const showDelay = 200
const hideDelay = 600

function Singleton() {
  const instanceRef = useRef()
  const showTimeoutRef = useRef()
  const hideTimeoutRef = useRef()
  const targetRef = useRef({})

  useEffect(() => {
    instanceRef.current = tippy(
      {},
      {
        content: 'Tooltip',
        updateDuration: 700,
      },
    )
    instanceRef.current.popper.style.transitionTimingFunction =
      'cubic-bezier(.23,1.42,.16,.96)'
  }, [])

  function clearTimeouts() {
    clearTimeout(showTimeoutRef.current)
    clearTimeout(hideTimeoutRef.current)
  }

  function show(event) {
    const instance = instanceRef.current
    const target = event.currentTarget

    // Window blur listener needs this
    delete targetRef.current._tippy
    targetRef.current = target
    target._tippy = instance

    Object.assign(instance.reference, {
      clientHeight: target.clientHeight,
      clientWidth: target.clientWidth,
      getBoundingClientRect: target.getBoundingClientRect.bind(target),
    })

    clearTimeouts()

    if (!instance.state.isVisible) {
      showTimeoutRef.current = setTimeout(() => {
        instance.show()
      }, showDelay)
    }

    if (instance.popperInstance) {
      instance.popperInstance.scheduleUpdate()
    }
  }

  function hide() {
    const instance = instanceRef.current

    clearTimeouts()

    hideTimeoutRef.current = setTimeout(() => {
      instance.hide()
    }, hideDelay)
  }

  return arr.map((_, i) => (
    <Button
      key={i}
      onMouseEnter={show}
      onMouseLeave={hide}
      onFocus={show}
      onBlur={hide}
    >
      Button
    </Button>
  ))
}

export default Singleton
